.products {
  width: 100%;
  position: absolute;
  z-index: 100;
  left: 0;
  top: 64px;
  opacity: 0;
  max-height: calc(100vh - 64px);
  transform: translateY(-12px);
  transition: all 0.3s ease-in;
  background: #fff;
  padding: 8px 0 0;
  overflow: auto;
  box-shadow: none;
  line-height: 1.5;
  font-weight: normal;
  white-space: normal;
  pointer-events: none;
}
.products .container {
  width: var(--container-max-width);
  max-width: calc(100% - var(--container-padding-large));
  margin-right: auto;
  margin-left: auto;
}
@media only screen and (max-width: 931.99px) {
  .products .container {
    max-width: calc(100% - var(--container-padding-medium));
  }
}
@media only screen and (max-width: 767.99px) {
  .products .container {
    max-width: calc(100% - var(--container-padding-small));
  }
}
.products.show {
  opacity: 1;
  box-shadow: 0 2px 8px #f0f1f2 inset;
  pointer-events: auto;
  transform: translateY(0);
  height: auto !important;
}
.products.show + .mask {
  opacity: 1;
}
.products.bannerVisible {
  max-height: calc(100vh - 102px);
  top: 102px;
}
.products.bannerVisible + .mask {
  top: 102px;
}
.products h3 {
  font-size: 14px;
  line-height: 22px;
  margin: 24px 0;
  color: #a3b1bf;
  font-weight: normal;
  padding-bottom: 8px;
  border-bottom: 1px solid #f2f4f5;
}
.products ul {
  list-style: none;
  padding: 0;
  margin: 0 0 20px 0;
}
.products ul:last-child {
  margin-bottom: 32px;
}
.products .product {
  overflow: hidden;
  width: 25%;
  display: inline-block;
  vertical-align: top;
  margin-bottom: 32px;
  margin-right: 0 !important;
  padding-right: 32px;
}
.products .product img {
  width: 32px;
  height: 32px;
  margin-right: 12px;
  float: left;
}
.products .product .productContent {
  overflow: hidden;
}
.products .product .productContent h4 {
  margin-bottom: 4px;
  font-size: 14px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.85);
  transition: all 0.3s;
}
.products .product .productContent h4:hover {
  color: #597ef7;
}
.products .product .productContent h4 .productSlogan {
  margin-left: 8px;
}
.products .product .productContent .productDescription {
  font-size: 12px;
  line-height: 20px;
  margin-bottom: 8px;
  color: #a3b1bf;
}
.products .product .productContent .productLinks > a {
  margin-right: 12px;
  font-size: 12px;
  line-height: 20px;
  transition: all 0.3s;
  display: inline-block;
  color: #8ba5f9;
}
.products .product .productContent .productLinks > a:hover {
  color: #597ef7;
}
.mask {
  background: rgba(13, 26, 38, 0.5);
  position: fixed;
  top: 64px;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s;
}
@media only screen and (max-width: 931.99px) {
  .products .product {
    width: 50%;
    padding-right: 24px;
  }
}
@media only screen and (max-width: 767.99px) {
  .products .product {
    width: 100%;
    padding-right: 0;
  }
}
