.mainContainer {
  display: flex;
}
.layout {
  margin: 24px 0 32px;
  min-height: 100vh;
}
.markdown {
  font-size: 14px;
  line-height: 2;
  min-height: 600px;
}
.markdown h1 {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  font-family: Avenir, var(--font-family), sans-serif;
  line-height: 38px;
}
.markdown h1:first-child {
  margin-top: 8px;
}
.markdown h2 {
  font-size: 24px;
  line-height: 32px;
}
.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6 {
  clear: both;
  margin: 1em 0 0.3em;
  font-weight: 500;
  font-family: Avenir, var(--font-family), sans-serif;
}
.markdown h3 {
  font-size: 20px;
  margin-bottom: 4px;
  font-weight: 600;
  line-height: 30px;
  border-bottom: 1px solid var(--border-color-split);
}
.markdown h4 {
  font-size: 16px;
  font-weight: 600;
  margin: 0;
}
.markdown h5 {
  font-size: 14px;
}
.markdown h6 {
  font-size: 12px;
}
.markdown hr {
  clear: both;
  height: 1px;
  margin: 56px 0;
  background: var(--border-color-split);
  border: 0;
}
.markdown p {
  line-height: 32px;
}
.markdown p,
.markdown pre {
  margin: 0.6em 0;
}
.markdown pre > code[class*='language-'] {
  line-height: 1.8;
}
.markdown pre[class*='language-'] {
  background-color: rgba(0, 0, 0, 0.03);
  width: 100%;
}
.markdown code {
  padding: 0.2em 0.4em;
  margin: 0;
  font-size: 85%;
  background-color: rgba(175, 184, 193, 0.2);
  border-radius: 6px;
}
.markdown code[class*='language-'] {
  background: none;
}
.markdown code[class*='language-text'] {
  white-space: break-spaces;
  word-break: break-word;
}
.markdown code.language-sign,
.markdown pre.language-sign {
  background: #e6f7ff;
}
.markdown pre.language-sign {
  border: 1px solid #91d5ff;
}
.markdown :global(.gatsby-highlight-code-line) {
  background-color: #f9f0ff;
  border-left: 2px solid #873bf4;
}
.markdown description {
  opacity: 0.68;
}
.markdown ul,
.markdown ol {
  margin: 0;
  padding: 0;
}
.markdown ul:not(:global(.ant-skeleton-paragraph)) > li {
  margin-left: 20px;
  padding-left: 4px;
  list-style-type: circle;
}
.markdown ul:not(:global(.ant-skeleton-paragraph)) > li:empty {
  display: none;
}
.markdown ol > li {
  margin-left: 20px;
  padding-left: 4px;
  list-style-type: decimal;
}
.markdown ul > li > p,
.markdown ol > li > p {
  margin: 0.2em 0;
}
.markdown strong,
.markdown b {
  font-weight: 500;
}
.markdown table {
  width: 100%;
  margin: 24px 0;
  empty-cells: show;
  border: 1px solid var(--border-color-split);
  border-collapse: collapse;
  border-spacing: 0;
}
.markdown table img {
  max-width: 100%;
}
.markdown table th {
  color: #5c6b77;
  font-weight: 500;
  background: rgba(0, 0, 0, 0.02);
}
.markdown table th,
.markdown table td {
  padding: 4px 12px;
  text-align: left;
  border: 1px solid var(--border-color-split);
  line-height: 24px;
}
.markdown table img {
  max-height: 100px;
}
.markdown table td:first-child {
  width: 20%;
  font-weight: 500;
  background: #fcfcfc;
  color: rgba(0, 0, 0, 0.85);
}
.markdown blockquote {
  margin: 0.5em 0;
  padding-left: 0.8em;
  color: var(--text-color-secondary);
  font-size: 90%;
  border-left: 4px solid var(--border-color-split);
}
.markdown blockquote p {
  margin: 0;
}
.markdown > br,
.markdown > p > br {
  clear: both;
}
.markdown img {
  max-width: 100%;
}
.main {
  width: 100%;
  padding-left: 48px;
  padding-right: 24px;
  overflow: hidden;
}
.main .meta {
  margin-bottom: 1.2em;
}
.tocSider {
  width: 260px !important;
}
.toc {
  width: 260px;
  float: right;
  font-size: 12px;
  background: #fff;
  max-height: 100vh;
  overflow: auto;
  position: sticky;
  top: 0;
}
.toc ul > li {
  list-style: none !important;
  padding: 0 !important;
  margin-left: -1px !important;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.toc ul > li a {
  color: var(--text-color);
}
.contentTitle {
  margin-top: 8px;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  line-height: 38px;
}
.sider {
  width: 280px !important;
  height: inherit;
}
.sider :global .ant-layout-sider-children {
  overflow: hidden;
}
.sider :global .ant-layout-sider-children:hover {
  overflow-y: auto;
}
.affix > div {
  height: 100%;
}
.affix .memuLabel {
  display: flex;
  align-items: center;
}
.affix .memuLabel .tag {
  position: absolute;
  right: 20px;
  background: #f9f0ff;
  color: #873bf4;
  border: 1px solid #873bf4;
  border-radius: 8px;
  height: 22px;
  font-size: 12px !important;
  padding: 0 8px;
  line-height: 20px;
}
.menuIcon {
  opacity: 0.6;
  font-size: 16px !important;
  position: relative;
  top: 1px;
  margin-right: 8px !important;
}
.menuDrawer :global(.drawer) {
  z-index: 9;
}
.menuDrawer :global(.ant-menu) {
  border-right: 0;
}
.menuDrawer .menuSwitch {
  position: absolute;
  top: 72px;
  right: -40px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 0;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.15);
  border-radius: 0 4px 4px 0;
}
.gallery {
  display: flex;
  flex-direction: row-reverse;
}
.gallery .galleryContent {
  flex: 1;
}
.gallery .anchor {
  width: 260px;
  padding-left: 24px;
}
.gallery .anchor :global .ant-anchor {
  font-size: 12px;
}
.gallery .anchor :global .ant-anchor-link {
  padding-top: 5px;
  padding-bottom: 5px;
}
.gallery .galleryList {
  list-style: none;
  margin: 0 -12px !important;
}
.gallery .galleryCard {
  display: inline-block;
  vertical-align: top;
  width: 25%;
  margin: 0 !important;
  padding: 0 !important;
}
.gallery .galleryCard .galleryCardLink {
  display: block;
  position: relative;
  cursor: pointer;
  transition: all 0.3s;
  margin: 12px;
}
.gallery .galleryCard .galleryCardLink > div {
  border: 1px solid var(--border-color-split);
  border-radius: 6px;
  overflow: hidden;
  height: calc(100vw / 9 - 20px);
  min-height: 130px;
  max-height: 280px;
  object-fit: cover;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
}
.gallery .galleryCard .galleryCardLink > div img {
  max-width: 100%;
  max-height: 100%;
  object-fit: scale-down;
}
.gallery .galleryCard .galleryCardLink:hover > div {
  transform: translateY(-4px);
  border-color: transparent;
  box-shadow: 0 6px 16px rgba(107, 147, 224, 0.14);
}
.gallery .galleryCard .galleryCardLink h4 {
  margin: 16px 0 0;
  text-align: center;
  color: #101424;
  font-size: 16px;
  line-height: 22px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.gallery .galleryCard .customRibbon {
  transform: rotate(45deg) translate(5px, -12px);
  border-radius: 0;
  width: 65px;
  height: 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  text-align: center;
}
.gallery .galleryCard .customRibbon :global .ant-ribbon-corner {
  display: none;
}
.apiStructure {
  padding: 0;
}
.editOnGtiHubButton {
  margin-left: 0.5em;
  font-size: 16px;
  color: #999;
  vertical-align: middle;
  position: relative;
  top: -2px;
}
.exampleContent {
  margin-top: -24px;
}
.exampleContent :global .SplitPane {
  overflow: auto;
  left: 0px;
  height: calc(100vh - 64px) !important;
}
.exampleContent :global .Pane2 {
  overflow: auto;
}
.exampleContent :global .ant-collapse-extra {
  margin-left: 16px;
  display: inline-block;
  float: none !important;
}
@media only screen and (max-width: 1199.99px) {
  .gallery .galleryCard {
    width: 33.3%;
  }
}
@media only screen and (max-width: 991.99px) {
  .markdown .main {
    width: 100vw !important;
    padding-left: 32px;
    padding-right: 32px;
  }
  .tocSiderbar {
    display: none;
  }
  .gallery .galleryCard {
    width: 50%;
  }
  .gallery .anchor {
    display: none;
  }
}
@media only screen and (max-width: 767.99px) {
  .layout {
    flex-direction: column !important;
  }
  .sider {
    margin-bottom: 32px;
    padding-bottom: 32px;
    border-bottom: 1px solid #e8e8e8;
  }
  .markdown .main {
    margin-top: 30px;
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media only screen and (max-width: 575.99px) {
  .gallery .galleryCard {
    width: 100%;
  }
}
.galleryAnchor {
  overflow-x: hidden;
  overflow-y: hidden;
}
.galleryAnchor:hover {
  overflow-y: auto;
}
.galleryAnchor :global(.ant-anchor-link) {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  align-items: center;
}
.galleryAnchor :global(.ant-anchor-ink) {
  display: none;
}
.galleryAnchor :global(.ant-anchor-link-title) {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  align-items: center;
}
.apiAnchor {
  padding-left: 14px;
  overflow: hidden;
}
.apiAnchor:hover {
  overflow-y: auto;
}
.apiAnchor :global(.ant-anchor-ink-ball) {
  width: 2px;
  height: 24px;
  background-color: var(--primary-color);
  border: none;
  border-radius: 0;
  transform: translateX(-50%) translateY(-8px);
}
.apiAnchor :global(.anticon) {
  position: absolute;
  padding-top: 5px;
  font-size: 12px;
  color: red;
}
.apiAnchor .parent {
  padding: 5px 0 5px 14px;
  font-size: 12px;
}
.apiAnchor .children {
  padding: 5px 0 5px 14px;
  font-size: 12px;
}
.backTop {
  color: #868484;
  background-color: #fff;
  box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
  transition: color 0.3s;
  width: 44px;
  height: 44px;
  line-height: 44px;
  font-size: 22px;
  border-radius: 50%;
  text-align: center;
}
.title {
  width: 100%;
  margin-top: 8px;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  line-height: 38px;
}
.content {
  width: 100% !important;
  min-width: 400px;
  justify-content: center;
  background-color: #fff;
}
.menuSwitch {
  position: absolute;
  top: 72px;
  right: -40px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 0;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.15);
  border-radius: 0 4px 4px 0;
}
.readtimeContainer {
  margin-bottom: 1.2em;
}
.readtime {
  color: #0d1a26;
  line-height: 1.5715;
  list-style: none;
  -webkit-font-feature-settings: 'tnum';
  font-feature-settings: 'tnum';
  display: inline-block;
  height: auto;
  margin: 0 8px 0 0;
  padding: 0 7px;
  font-size: 12px;
  line-height: 20px;
  white-space: nowrap;
  background: #fafafa;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  opacity: 1;
  transition: all 0.3s;
  background-color: #fff;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
}
