@keyframes animated-line {
  to {
    stroke-dashoffset: -1000;
  }
}
.x6-node-selected rect {
  stroke: #239edd;
}
.x6-edge-selected path:nth-child(2) {
  stroke: #239edd;
  stroke-width: 1.5px;
}
.x6-widget-snapline-horizontal,
.x6-widget-snapline-vertical {
  stroke: #239edd;
}
.x6-widget-transform {
  margin: -1px 0 0 -1px;
  padding: 0px;
  border: 1px solid #239edd;
}
.x6-widget-transform > div {
  border: 1px solid #239edd;
}
.x6-widget-transform > div:hover {
  background-color: #3dafe4;
}
.x6-widget-transform-active-handle {
  background-color: #3dafe4;
}
.x6-widget-transform-resize {
  border-radius: 0;
}
.toolButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 1px solid #e6e8eb;
  border-radius: 6px;
}
.toolButton button {
  min-width: 36px !important;
  height: 36px;
  border: 1px solid #fff;
  border-radius: 4px;
  background-color: #fff;
  outline: none;
  user-select: none;
  touch-action: manipulation;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
}
.toolButton button:disabled {
  background-color: rgba(0, 0, 0, 0.04);
  color: rgba(0, 0, 0, 0.25);
}
.toolButton button:hover {
  border: 1px solid #4096ff;
}
.toolButton .dropDownBtn {
  width: 100%;
  padding: 0;
}
.toolButton .tippy-box {
  border: none !important;
  box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 9px 28px 8px rgba(0, 0, 0, 0.05) !important;
  background-color: #ffffff;
}
.toolButton .tippy-content {
  width: 60px;
  padding: 4px !important;
}
.toolButton .tippyBtnContent {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.toolButton .tippyBtnContent button {
  width: 100%;
  min-width: 36px !important;
}
.toolButton .tippyBtnContent button:hover {
  border: none !important;
  background-color: rgba(0, 0, 0, 0.04);
}
.toolbuttonVertical {
  flex-direction: column;
}
.toolbuttonVertical button {
  min-width: 40px !important;
}
