.news {
  width: 50%;
  margin-right: 2%;
  background-color: #ffffff;
  border: 1px solid #e5e8ef;
  box-shadow: 0 8px 28px 0 rgba(0, 0, 0, 0.05);
  border-radius: 16px;
  height: 100%;
  font-size: 14px;
}
.news:last-child {
  margin-right: 0;
}
.news .container {
  height: 100%;
  color: #1d2129;
  letter-spacing: 0;
}
.news .container .content {
  height: 100%;
  padding: 0 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.news .container .content .description {
  top: 0;
  margin-bottom: 0;
  transition: all 0.3s;
  font-family: PingFangSC;
  font-weight: 500;
  font-size: 20px;
  text-align: center;
  position: relative;
}
.news .container .content .description > img {
  width: 24px;
  height: 24px;
  position: relative;
  top: -2px;
  left: -4px;
}
.news .container .content .date,
.news .container .content .subTitle {
  color: #424e66;
  bottom: 0;
  font-size: 1em;
  margin-top: 4px;
  margin-bottom: 0;
  text-align: center;
}
.news:hover .description {
  color: #691eff;
}
@media (max-width: 900px) {
  .news {
    position: absolute;
    width: 100%;
  }
  .news:nth-child(1) {
    animation: showAndHide0 9s infinite;
  }
  .news:nth-child(2) {
    top: 44px;
    animation: showAndHide1 9s infinite;
  }
  .news:nth-child(3) {
    top: 44px;
    animation: showAndHide2 9s infinite;
  }
}
@media (max-width: 768px) {
  .news {
    width: 100%;
  }
}
@keyframes showAndHide0 {
  0% {
    opacity: 1;
    top: 0px;
    z-index: 1;
  }
  33.3% {
    opacity: 0;
    top: 0px;
    z-index: 0;
  }
  66.6% {
    opacity: 0;
    top: 30px;
    z-index: 0;
  }
  100% {
    opacity: 1;
    top: 0px;
    z-index: 1;
  }
}
@keyframes showAndHide1 {
  0% {
    opacity: 0;
    top: 30px;
    z-index: 0;
  }
  33.3% {
    opacity: 1;
    top: 0px;
    z-index: 1;
  }
  66.6% {
    opacity: 0;
    top: 0px;
    z-index: 0;
  }
  100% {
    opacity: 0;
    top: 30px;
    z-index: 0;
  }
}
@keyframes showAndHide2 {
  0% {
    opacity: 0;
    top: 30px;
    z-index: 0;
  }
  33.3% {
    opacity: 0;
    top: 30px;
    z-index: 0;
  }
  66.6% {
    opacity: 1;
    top: 0px;
    z-index: 1;
  }
  100% {
    opacity: 0;
    top: 0px;
    z-index: 0;
  }
}
