.card {
  background-color: #fff;
  height: 100%;
  padding: 36px 70px;
  box-sizing: border-box;
}
.card .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.card .content .icon {
  height: 60px;
}
.card .content .title {
  font-size: 1.714em;
  color: #000;
  margin-top: 28px;
  margin-bottom: 11px;
  font-family: AlibabaPuHuiTiM;
  color: #1d2129;
  text-align: center;
  line-height: 32px;
}
.card .content .description {
  opacity: 0.6;
  width: 100%;
  color: #697b8c;
  font-size: 1em;
  line-height: 1.8em;
  margin-top: 11px;
  margin-bottom: 0;
}
@media (max-width: 900px) {
  .cards .cardWrapper .card {
    margin: 84px 19px 80px 19px;
  }
  .cards .cardWrapper .card .content .icon {
    width: 25.6%;
  }
  .cards .cardWrapper .card .content .title {
    font-size: 1.4em;
    margin-top: 28px;
  }
  .cards .cardWrapper .card .content .description {
    font-size: 1em;
    padding-bottom: 0px;
    padding-top: 0px;
    margin-top: 12px;
  }
}
