.footer {
  font-family: var(--font-family);
}
.footer :global(.rc-footer-container),
.footer :global(.rc-footer-bottom-container) {
  width: var(--container-max-width);
  max-width: calc(100% - var(--container-padding-large));
  margin-right: auto;
  margin-left: auto;
}
@media only screen and (max-width: 931.99px) {
  .footer :global(.rc-footer-container),
  .footer :global(.rc-footer-bottom-container) {
    max-width: calc(100% - var(--container-padding-medium));
  }
}
@media only screen and (max-width: 767.99px) {
  .footer :global(.rc-footer-container),
  .footer :global(.rc-footer-bottom-container) {
    max-width: calc(100% - var(--container-padding-small));
  }
}
.footer :global(.rc-footer-bottom-container) {
  border-top: 4px solid #f0f2ff;
}
.footer.light :global(.rc-footer-item) > a > span {
  font-size: 16px;
}
.footer.light :global(.rc-footer-item) > a:hover {
  color: #691eff;
}
.footer .description {
  opacity: 0.6;
  font-weight: normal;
  margin-left: 0.5em;
}
.footer .more {
  display: flex;
  justify-content: center;
  font-size: 14px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  margin-bottom: 16px;
  box-sizing: border-box;
  padding-bottom: 16px;
  overflow: hidden;
  flex-wrap: wrap;
}
.footer .more .title {
  font-size: 15px;
  color: #fff;
  margin-right: 8px;
}
.footer .more :global(.anticon),
.footer .more a {
  margin-right: 5px;
  color: #fff;
}
.footer .more img {
  width: 14px;
  height: 14px;
  margin-right: 4px;
  position: relative;
  top: -2px;
}
.footer .bottom {
  display: flex;
  justify-content: center;
  font-size: 14px;
}
.footer .bottom :global(.anticon),
.footer .bottom a {
  margin-right: 8px;
  color: rgba(255, 255, 255, 0.6);
}
.footer .bottom.light :global(.anticon),
.footer .bottom.light a {
  color: #424e66;
}
.footer .bottom.light :global(.anticon):hover,
.footer .bottom.light a:hover {
  color: #691eff;
}
@media only screen and (max-width: 767.99px) {
  .footer .bottom {
    display: block;
  }
}
.withMenu {
  margin-left: 0;
}
@media only screen and (min-width: 767.99px) {
  .withMenu {
    margin-left: 240px;
  }
}
@media only screen and (min-width: 991.99px) {
  .withMenu {
    margin-left: 280px;
  }
}
