.api {
  height: 100%;
  color: rgba(0, 0, 0, 0.85);
}
.api .header {
  position: relative;
  height: 36px;
  background-color: #f8f9fc;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  padding-right: 12px;
  padding-left: 36px;
  color: #777;
  line-height: 36px;
}
.api .content {
  overflow: hidden;
  border-left: 1px solid #ececec;
  height: calc(100% - 36px);
  overflow-y: scroll;
}
.api .content .disorganized {
  padding: 0 12px;
}
.api .content .disorganized > :first-child {
  margin-top: 12px;
}
.api .content > hr {
  display: none;
}
.api .content p {
  margin-bottom: 0;
}
.api .content h1,
.api .content h2,
.api .content h3,
.api .content h4,
.api .content h5,
.api .content h6 {
  font-weight: 400;
  margin-bottom: 0;
  margin-top: 8px;
  color: rgba(0, 0, 0, 0.85);
  border-top: 1px solid #f8f9fc;
}
.api .content h1:first-child,
.api .content h2:first-child,
.api .content h3:first-child,
.api .content h4:first-child,
.api .content h5:first-child,
.api .content h6:first-child {
  margin-top: 0;
  border-top: none;
}
.api .content table {
  border-width: 1px;
  border-color: #ececec;
  border-right-style: solid;
  border-top-style: solid;
  margin-bottom: 6px;
}
.api .content td,
.api .content th {
  border-width: 1px;
  border-color: #ececec;
  text-align: left;
  border-left-style: solid;
  border-bottom-style: solid;
  padding: 4px;
}
.api img {
  max-width: 100%;
}
.api :global .ant-collapse-ghost > .ant-collapse-item > .ant-collapse-content > .ant-collapse-content-box {
  padding: 0 4px 0 24px;
}
.api :global .ant-collapse > .ant-collapse-item > .ant-collapse-header {
  padding: 8px;
}
.api :global .ant-collapse > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
  margin-right: 6px;
}
