.preview {
  position: relative;
}
.preview .ul {
  position: absolute;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: -28px;
  left: 0;
  top: 0;
  display: none;
  background: #eee;
  width: 28px;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
.preview .ul .li {
  list-style-type: none !important;
  cursor: pointer;
  margin: 0 !important;
  padding: 0 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
}
.preview .ul .li:hover {
  color: #873bf4;
}
.preview .ul .li:active {
  transform: scale(1.1);
}
.preview .ul:hover {
  display: block;
}
.preview .loading {
  display: block;
  color: #873bf4;
  padding: 0.25rem 0;
  border-left: 3px solid #873bf4;
  padding-left: 12px;
}
.preview .error {
  display: block;
  color: #fb1716;
  padding: 0.25rem 0;
  border-left: 3px solid #ff0000;
  padding-left: 12px;
}
