.wrapper {
  display: flex;
  margin: auto;
  position: relative;
  flex-direction: column;
  padding: 0;
  height: auto;
  overflow-x: hidden;
}
.wrapper .content {
  height: 100%;
  margin-bottom: 5%;
  position: relative;
  width: var(--container-max-width);
  max-width: calc(100% - var(--container-padding-large));
  margin-right: auto;
  margin-left: auto;
  padding-left: 4.5%;
}
@media only screen and (max-width: 931.99px) {
  .wrapper .content {
    max-width: calc(100% - var(--container-padding-medium));
  }
}
@media only screen and (max-width: 767.99px) {
  .wrapper .content {
    max-width: calc(100% - var(--container-padding-small));
  }
}
.wrapper .title {
  font-size: 2.714em;
  font-weight: 500;
  margin: 118px 0 0 0;
  position: relative;
  height: min-content;
  text-align: center;
  color: #000000;
}
.wrapper .companiesContainer {
  position: relative;
  margin: auto;
  margin-top: 48px;
}
.wrapper .companiesGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px 124px;
  width: 100%;
}
@media only screen and (max-width: 992px) {
  .wrapper .companiesGrid {
    grid-template-columns: repeat(4, 1fr);
    gap: 10px 50px;
  }
}
@media only screen and (max-width: 768px) {
  .wrapper .companiesGrid {
    grid-template-columns: repeat(3, 1fr);
    gap: 10px 77px;
  }
}
@media only screen and (max-width: 576px) {
  .wrapper .companiesGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px 77px;
  }
}
.wrapper .company {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px;
}
.wrapper .companyimg {
  width: 100%;
  height: auto;
  object-fit: contain;
}
@media (max-width: 768px) {
  .wrapper .title {
    font-size: 1.9rem;
    margin: 98px 0 0 0;
  }
  .wrapper .content {
    margin-left: 4.27%;
    width: 91.64%;
    max-width: 91.64%;
  }
  .wrapper .companiesContainer {
    margin-top: 50px;
  }
  .wrapper .companiesContainer .company .companyimg {
    width: 100%;
  }
}
