.wrapper {
  min-height: 650px;
  max-height: 803px;
  background: linear-gradient(225deg, #ffffff, #f0f5ff);
  height: calc(94vh);
  position: relative;
  overflow: hidden;
}
.wrapper .content {
  width: var(--container-max-width);
  max-width: calc(100% - var(--container-padding-large));
  margin-right: auto;
  margin-left: auto;
  height: 100%;
  position: relative;
}
@media only screen and (max-width: 931.99px) {
  .wrapper .content {
    max-width: calc(100% - var(--container-padding-medium));
  }
}
@media only screen and (max-width: 767.99px) {
  .wrapper .content {
    max-width: calc(100% - var(--container-padding-small));
  }
}
.wrapper .text {
  position: relative;
  top: 26%;
  z-index: 1;
  margin-left: 4.5%;
}
.wrapper .text .title {
  font-size: 3.4em;
  color: #000000;
  position: relative;
  font-family: AlibabaPuHuiTiRHeavy;
}
.wrapper .text .title .engine {
  color: #691eff;
}
.wrapper .text .description {
  margin-top: 0.83%;
  margin-bottom: 0px;
  color: #6a7b8c;
  font-weight: 200;
  position: relative;
  width: 40%;
  max-width: 700px;
  font-family: AlibabaPuHuiTiR;
  font-size: 18px;
  color: #424e66;
  max-height: 145px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 4;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
.wrapper .text .buttons {
  display: flex;
  margin-top: max(3%, 40px);
}
.wrapper .text .buttons .buttonLink {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  z-index: 10;
  position: relative;
  transition: all 0.3s;
  text-align: center;
  height: 54px;
  min-width: 166px;
  padding: 0 10px;
  box-shadow: 0 8px 28px 0 rgba(0, 0, 0, 0.1);
  background-color: #691eff;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 0 8px 28px 0 #f2f2f2;
  margin-right: 20px;
  color: #ffffff;
  overflow: hidden;
}
.wrapper .text .buttons .buttonLink .icon {
  width: 20px;
  height: 20px;
  background-image: url(https://mdn.alipayobjects.com/huamei_qa8qxu/afts/img/A*lo_5RYdqKLsAAAAAAAAAAAAADmJ7AQ/original);
  margin-right: 6px;
}
.wrapper .text .buttons .buttonLink:hover {
  opacity: 0.8;
}
.wrapper .text .buttons .buttonLink.primary {
  color: #fff;
  background-color: #ffffff;
  color: #000;
  border: 1px solid #e8e8e8;
}
.wrapper .text .buttons .buttonLink.primary .icon {
  position: relative;
  background-image: url(https://mdn.alipayobjects.com/huamei_qa8qxu/afts/img/A*_xFsQbWE_AYAAAAAAAAAAAAADmJ7AQ/original);
}
.wrapper .text .buttons .buttonLink.primary:hover {
  color: #691eff;
}
.wrapper .text .buttons .buttonLink.primary:hover .icon {
  left: -80px;
  filter: drop-shadow(#691eff 80px 0);
}
.wrapper .text .buttons .githubWrapper {
  display: flex;
}
.wrapper .text .buttons .githubWrapper :global .github-btn {
  display: flex;
  height: 54px;
  cursor: pointer;
  box-shadow: 0 8px 28px 0 rgba(0, 0, 0, 0.05);
  background-color: #ffffff;
  border: 1px solid #e8e8e8;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  overflow: hidden;
  transition: all 0.15s;
}
.wrapper .text .buttons .githubWrapper :global .github-btn:hover .gh-btn > .gh-ico {
  filter: drop-shadow(#691eff 80px 0);
}
.wrapper .text .buttons .githubWrapper :global .github-btn:hover .gh-count {
  color: #691eff;
}
.wrapper .text .buttons .githubWrapper :global .github-btn .gh-btn {
  padding: 16px;
}
.wrapper .text .buttons .githubWrapper :global .github-btn .gh-btn .gh-ico {
  display: block;
  width: 20px;
  height: 20px;
  background-image: url(https://gw.alipayobjects.com/mdn/rms_f8c6a0/afts/img/A*Nk9mQ48ZoZMAAAAAAAAAAABkARQnAQ);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  position: relative;
  left: -80px;
  filter: drop-shadow(#1d2129 80px 0);
}
.wrapper .text .buttons .githubWrapper :global .github-btn .gh-btn .gh-text {
  display: none;
}
.wrapper .text .buttons .githubWrapper :global .github-btn .gh-count {
  height: 54px;
  line-height: 22px;
  padding: 16px 16px 16px 0;
  position: relative;
  font-size: 18px;
  margin-left: -8px;
  color: #1d2129;
  letter-spacing: 0;
  font-family: AlibabaSans102-Medium;
}
.wrapper .news {
  width: 100%;
  display: flex;
  bottom: 30px;
  position: absolute;
  z-index: 3;
  height: 128px;
  box-sizing: border-box;
  padding: 0 4.5%;
}
.wrapper .teaser {
  display: flex;
  position: absolute;
  right: 0;
  margin-top: -300px;
  width: 52%;
  max-width: 598px;
  height: 324px;
  margin: auto;
  margin-top: 0;
  top: 0;
  bottom: 0;
}
.wrapper .teaser .teaserimg {
  width: 100%;
  height: auto;
  position: relative;
}
.wrapper .backLeftBottom {
  position: absolute;
  left: -8%;
  bottom: -20px;
}
@media (max-width: 1144px) {
  .wrapper .teaser {
    transition: 0.3s all;
    margin: auto;
    margin-top: 0;
    top: 0;
    bottom: 0;
  }
}
@media (max-width: 900px) {
  .wrapper .content {
    width: 91.46%;
    margin-left: 4.27%;
    margin-bottom: 5%;
    position: relative;
  }
  .wrapper .text {
    top: 16.7%;
    text-align: center;
    margin-left: 0;
  }
  .wrapper .text .title {
    font-size: 2.857em;
    margin-left: 0;
  }
  .wrapper .text .description {
    width: 100%;
    max-width: 100%;
    margin-left: 0;
    font-size: 1em;
  }
  .wrapper .text .buttons {
    display: inline-flex;
  }
  .wrapper .text .buttons .more {
    font-size: 1em;
  }
  .wrapper .text .buttons .githubIframe {
    height: 40px;
  }
  .wrapper .text .buttons .videoButtonWrapper {
    width: fit-content;
    height: fit-content;
  }
  .wrapper .news {
    width: 91%;
    padding: 0;
    display: block;
    bottom: 10x;
    left: 4.5%;
  }
  .wrapper .teaser {
    width: 100%;
    max-width: 1200px;
    margin-top: auto;
    margin-left: unset;
    margin-right: unset;
    right: unset;
    top: 30%;
    display: flex;
    justify-content: center;
  }
  .wrapper .teaser .teaserimg {
    width: 40%;
    margin-left: unset;
    margin-right: unset;
  }
}
@media (max-width: 786px) {
  .wrapper .text .buttons .videoButtonWrapper {
    display: none;
  }
}
@media (max-width: 510px) {
  .wrapper .text .buttons {
    margin-top: 70%;
    position: relative;
    top: -170px;
  }
  .wrapper .text .buttons .githubWrapper {
    display: none;
  }
  .wrapper .text .buttons .buttonLink:nth-child(2) {
    margin-right: 0;
  }
  .wrapper .teaser {
    width: 100%;
    top: 5%;
  }
  .wrapper .teaser .teaserimg {
    width: 100%;
  }
  .wrapper .backLeftBottom {
    display: none;
  }
}
@media (max-width: 480px) {
  .wrapper .text {
    height: 70%;
  }
  .wrapper .news {
    height: 160px;
  }
  .wrapper .teaser {
    top: 10%;
  }
}
