.search {
  margin-left: 36px;
  line-height: 1.5;
  white-space: nowrap;
  transition: all 0.3s;
}
.search .icon {
  color: #a3b1bf;
  opacity: 0.75;
  margin-right: 8px;
}
.search .input {
  border: none;
  height: 32px;
  line-height: 32px;
  outline: none;
  background: transparent;
  vertical-align: baseline !important;
}
.search .input::placeholder {
  color: #a3b1bf;
}
@media only screen and (max-width: 931.99px) {
  .search {
    display: none;
  }
}
