.header {
  background: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  padding: 0;
  position: relative;
  z-index: 200;
  font-family: Avenir, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  transition: box-shadow 0.3s, background 0.3s;
}
.header.transparent {
  background: transparent;
  box-shadow: none;
}
.header.fixed {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: #fff;
}
.header .container {
  max-width: calc(100% - 80px);
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 64px;
  line-height: 64px;
}
.header.isHomePage {
  margin-bottom: -64px;
}
.header.isHomePage .container {
  width: var(--container-max-width);
  max-width: calc(100% - var(--container-padding-large));
  margin-right: auto;
  margin-left: auto;
}
@media only screen and (max-width: 931.99px) {
  .header.isHomePage .container {
    max-width: calc(100% - var(--container-padding-medium));
  }
}
@media only screen and (max-width: 767.99px) {
  .header.isHomePage .container {
    max-width: calc(100% - var(--container-padding-small));
  }
}
.header.lightTheme {
  margin-bottom: -64px;
}
.header.lightTheme nav a,
.header.lightTheme nav span,
.header.lightTheme nav .versions {
  color: #424e66 !important;
}
.header.lightTheme nav .translation {
  fill: #424e66 !important;
}
.header.isAntVHome {
  margin-bottom: -64px;
}
.header.isAntVHome .container {
  max-width: 100%;
  padding: 0 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.header.isAntVHome .nav .menu > li {
  margin-right: 8px;
  font-size: 16px;
}
.header.isAntVHome .nav .menu > li > :first-child {
  display: flex;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0 12px;
  height: 32px;
  line-height: 32px;
  border-radius: 6px;
  color: #424e66;
}
.header.isAntVHome .nav .menu > li > :first-child:hover {
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
}
.header.isAntVHome .nav .menu .navIcon > :first-child {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  padding: 0;
}
.header.isAntVHome .nav .menu .versions {
  margin-right: 0;
}
.header.isAntVHome .nav .menu .languageSwitcher {
  position: relative;
  top: 1px;
}
.header.isAntVHome .nav .menu .wxQrcode {
  position: relative;
  top: 2px;
}
.header.isAntVHome .nav .menu .githubCorner {
  position: relative;
  top: 4px;
}
.header .banner {
  width: 100%;
  background: #daf5eb;
  text-align: center;
  word-break: keep-all;
}
.header .left {
  display: flex;
  align-items: center;
  height: 100%;
}
.header .left h1 {
  margin: 0;
  height: 100%;
}
.header .left h1 a {
  display: block;
  height: 100%;
}
.header .left h1 a svg {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -1.5px;
}
.header .left h1 a img {
  height: 30px;
  position: relative;
  top: -1.5px;
}
.header .left .divider {
  width: 1px;
  height: 24px;
  background-color: #ccc;
  display: inline-block;
  margin: 0 20px;
  transition: all 0.3s;
}
.header .left .subProduceName {
  margin: 0;
  white-space: nowrap;
  font-size: 18px;
}
.header .left .subProduceName a {
  color: rgba(0, 0, 0, 0.85);
  transition: all 0.3s;
}
.header .left .subProduceName a:hover {
  color: #000000;
}
.header .nav {
  display: flex;
  align-items: center;
  overflow: hidden;
}
.header .nav .menu {
  font-weight: normal;
  color: #697b8c;
  margin: 0;
  padding: 0;
  list-style: none;
  white-space: nowrap;
}
.header .nav .menu li {
  display: inline-block;
  margin-right: 28px;
  transition: box-shadow 0.3s;
  box-shadow: 0 2px 0 transparent inset;
}
.header .nav .menu li.activeItem {
  box-shadow: 0 2px 0 var(--primary-color) inset;
}
.header .nav .menu li.activeItem a {
  color: var(--primary-color);
}
.header .nav .menu li a {
  color: #697b8c;
  transition: all 0.3s;
  display: block;
}
.header .nav .menu li a:hover {
  color: var(--primary-color);
}
.header .nav .menu li:last-child {
  margin-right: 0;
}
.header .nav .menu .ecoSystems {
  cursor: pointer;
  transition: all 0.3s;
}
.header .nav .menu .ecoSystems svg {
  opacity: 0.3;
}
.header .nav .menu .ecoSystems:hover {
  color: var(--primary-color);
}
.header .nav .menu .ecoSystems:hover svg {
  color: #697b8c;
  opacity: 0.5;
}
.header .nav .menu .githubCorner i {
  font-size: 20px;
  position: relative;
  top: 2px;
}
.header .nav .menu .githubStar::after {
  display: block;
  content: '';
  clear: both;
}
.header .nav .menu .githubStar > span {
  float: left;
  height: 28px;
  transition: all 0.3s;
}
.header .nav .menu .translation {
  fill: #697b8c;
  width: 20px;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -1px;
}
.header .nav .menu .translation .translationIcon {
  font-size: 20px;
}
.header .nav .menu .translation:hover {
  fill: var(--primary-color);
}
.header .nav .menu .versions {
  cursor: pointer;
  transition: all 0.3s;
}
.header .nav .menu .versions svg {
  opacity: 0.3;
}
.header .nav .menu .versions:hover {
  color: var(--primary-color);
}
.header .nav .menu .versions:hover svg {
  color: #697b8c;
  opacity: 0.5;
}
.header .nav .menu .arrow {
  width: 14px;
  margin-left: 2px;
  position: relative;
  top: -2px;
  transition: all 0.3s;
}
.header .nav .menu .arrow.open {
  transform: rotate(180deg);
}
.header .nav .menu.popup {
  position: fixed;
  top: 64px;
  left: 0;
  right: 0;
  background: #fff;
  padding: 8px 0;
  text-align: center;
  transition: all 0.3s;
  height: calc(100vh - 64px);
  overflow: auto;
  opacity: 1;
}
.header .nav .menu.popup li {
  display: block;
  line-height: 64px;
  margin-right: 0;
  font-size: 16px;
}
.header .nav .menu.popup li.activeItem {
  box-shadow: none;
  background: #f3ebfe;
}
.header .nav .menu.popup li.activeItem a {
  color: var(--primary-color);
}
.header .nav .menu.popup li .productsMenu {
  position: static !important;
  text-align: left;
  max-height: none;
  height: 0;
}
.header .nav .menu.popup li .productsMenu + div {
  display: none;
}
.header .nav .menu.popup.popupHidden {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: all 0.3s;
}
.header .nav .menuIcon {
  font-size: 24px;
  cursor: pointer;
  color: #666;
  padding: 0 8px;
  transition: all 0.3s;
}
.header .nav .menuIcon:hover {
  color: #222;
}
.header .topAlert {
  display: flex;
  height: 22px;
  justify-content: center;
  align-items: center;
}
.header .topAlert > img {
  width: 16px;
  height: 16px;
  margin-right: 8px;
}
.header .topAlert > a {
  margin-left: 8px;
}
.remindHref {
  margin: 0px 6px 0px 5px;
}
.modalContent {
  padding: 16px;
}
@media only screen and (max-width: 1024px) {
  .header .nav .menu li {
    margin-right: 24px;
  }
}
@media only screen and (max-width: 931.99px) {
  .header .nav .menu li {
    margin-right: 16px;
  }
  .header .left .divider {
    margin: 0 12px;
  }
  .header .left .subProduceName {
    font-size: 16px;
    margin-right: 8px;
  }
  .header .container {
    max-width: calc(100% - 60px);
  }
}
@media only screen and (max-width: 767.99px) {
  .header .container {
    max-width: calc(100% - 32px);
  }
  .header .menu {
    opacity: 0;
    height: 0;
    overflow: hidden;
    position: fixed;
  }
}
