.searchResult {
  font-size: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 486px;
  max-width: 512px;
  max-height: 50vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.searchResult .item {
  width: 100%;
  display: flex;
  margin: 6px 0;
  position: relative;
}
.searchResult .item .subject {
  text-align: right;
  padding: 4px;
  flex-basis: 120px;
  color: #a0a1a7;
}
.searchResult .item .br {
  flex-basis: 1px;
  margin: 0 6px;
  background-color: #873bf4;
  opacity: 0.2;
}
.searchResult .item .result {
  flex: 1;
  min-width: 300px;
  padding: 4px 0 4px 6px;
  border-radius: 4px;
  overflow: hidden;
  cursor: pointer;
}
.searchResult .item .result:hover {
  background-color: #f9f0ff;
}
.searchResult .item .result .title {
  font-weight: 500;
  color: #000;
}
.searchResult .item .result .description {
  font-size: 11px;
  color: #a0a1a7;
  max-height: 55px;
  overflow: hidden;
}
.searchResult .item .result .description .segment {
  padding-left: 1px;
  padding-right: 1px;
}
.searchResult .item .result .highlighted {
  color: #873bf4;
}
.searchResult .empty {
  height: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  color: #a0a1a7;
  font-size: 16px;
}
