.preview {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 24px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}
.preview .header :global(.ant-page-header) {
  padding-left: 0;
}
.preview .content {
  position: relative;
  flex: 1;
  min-height: 0;
}
.preview .content .playgroundScriptContainer,
.preview .content .playgroundCodeContainer {
  position: relative;
  width: 100%;
  height: 100%;
}
.preview .content .playgroundScriptContainer :global(.playgroundCodeContainer),
.preview .content .playgroundCodeContainer :global(.playgroundCodeContainer) {
  position: relative;
  width: 100%;
  height: 100%;
}
.preview .content .playgroundScriptContainer :global(#container),
.preview .content .playgroundCodeContainer :global(#container) {
  position: relative;
  width: 100%;
  height: 100%;
}
.preview .content .result {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
}
