:global(#root) {
  width: 100%;
  height: 100%;
}
.example {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.example .container {
  flex: 1;
  background-color: #fff;
}
.example .container .content {
  position: relative;
  height: 100%;
}
.example .container .content .left {
  height: 100%;
}
.example .container .content .right {
  height: 100%;
}
.menuSider {
  background-color: #f8f9fc;
  border-right: 1px solid #ececec;
}
.menuSider :global .ant-layout-sider-children {
  overflow-y: hidden;
  overflow-x: hidden;
}
.menuSider :global .ant-layout-sider-children:hover {
  overflow-y: auto;
}
.trigger {
  font-size: 12px;
  position: relative;
  margin: auto;
  color: #6c7f90;
  left: -1px;
  z-index: 4;
  width: 22px;
  cursor: pointer;
  transition: color 0.3s;
  border: 1px solid #ececec;
  border-left: none;
  background-color: #fff;
  height: 66px;
  box-sizing: border-box;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  text-align: center;
}
.trigger[type='menu-unfold'] {
  background-color: #f8f9fc;
}
.trigger svg {
  position: absolute;
  left: 3px;
  top: 25px;
}
.trigger:hover {
  color: var(--primary-color);
}
