.wrapper {
  height: 667px;
  margin: auto;
  position: relative;
  padding: 0;
  overflow: hidden;
  background: #fafbfc;
}
.wrapper .slider {
  width: var(--container-max-width);
  max-width: calc(100% - var(--container-padding-large));
  margin-right: auto;
  margin-left: auto;
}
@media only screen and (max-width: 931.99px) {
  .wrapper .slider {
    max-width: calc(100% - var(--container-padding-medium));
  }
}
@media only screen and (max-width: 767.99px) {
  .wrapper .slider {
    max-width: calc(100% - var(--container-padding-small));
  }
}
.wrapper .appWrapper {
  margin-left: 5%;
  margin-bottom: 5%;
  width: 83.88%;
  height: 100%;
  overflow-y: hidden;
  display: flex !important;
  align-items: center;
}
.wrapper .appWrapper .appLeft {
  margin-left: 3.06%;
  width: 27%;
  margin-top: 8%;
  height: 613px;
}
.wrapper .appWrapper .appLeft .appContent {
  height: fit-content;
  position: relative;
}
.wrapper .appWrapper .appLeft .appContent .appLogo {
  width: 80px;
}
.wrapper .appWrapper .appLeft .appContent .appTitle {
  font-size: 1.2857em;
  font-weight: 500;
  margin: 48px 0 0 0;
  position: relative;
  height: min-content;
  color: #000000;
  user-select: text;
}
.wrapper .appWrapper .appLeft .appContent .appDescription {
  color: rgba(105, 123, 140, 0.6);
  font-size: 1em;
  margin: 16px 0 0 0;
  user-select: text;
}
.wrapper .appWrapper .appLeft .appContent .detailWrapper {
  margin: 20px 0 0 0;
}
.wrapper .appWrapper .appLeft .appContent .detailWrapper .detail {
  color: var(--primary-color);
  font-size: 1em;
}
.wrapper .appWrapper .appLeft .buttons {
  cursor: pointer;
  position: absolute;
  display: flex;
  bottom: 30%;
  height: 100px;
}
.wrapper .appWrapper .appLeft .buttons .controlButton {
  width: 47px;
  height: 47px;
  line-height: 47px;
  border: 1px solid #a3b1bf;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper .appWrapper .appLeft .buttons .controlButton:hover {
  border: 1px solid #ced4d9;
  fill: #000000;
}
.wrapper .appWrapper .appLeft .buttons .controlButton:hover :only-child {
  color: #ced4d9;
}
.wrapper .appWrapper .appLeft .buttons .controlButton .controlButtonIcon {
  color: #a3b1bf;
  transition: all 0.3s;
}
.wrapper .appWrapper .appLeft .buttons .nextButton {
  width: 47px;
  margin-left: -1px;
  background-image: url('https://gw.alipayobjects.com/zos/basement_prod/27ce6e21-bbb6-4490-8326-43483ac39e0b.svg');
  background-size: 100% 100%;
}
.wrapper .appWrapper .appLeft .buttons .nextButton:hover {
  background-image: url('https://gw.alipayobjects.com/zos/basement_prod/4d1f0d0a-4e56-4f21-a9aa-06b2f8c3e18e.svg');
}
.appTeaser {
  position: absolute;
  height: 667px;
  right: 0;
}
.slider {
  height: 100%;
}
.slider :global .slick-dots {
  display: none;
}
:global .slick-current {
  z-index: 2 !important;
}
@media (max-width: 1200px) {
  .wrapper .appWrapper .appLeft {
    width: 270px;
  }
  .appTeaser {
    position: absolute;
    width: 857px;
    left: 350px;
  }
}
@media (max-width: 768px) {
  .wrapper {
    width: 100%;
    height: fit-content;
  }
  .wrapper .slider {
    width: 100%;
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
  .wrapper .appWrapper {
    width: 100%;
    padding-bottom: 150px;
    margin-left: 0;
    flex-direction: column;
    text-align: center;
  }
  .wrapper .appWrapper .appLeft {
    height: 286px;
    margin-left: 4.27%;
    width: 91.47%;
    margin-top: 64px;
  }
  .wrapper .appWrapper .appLeft .appContent {
    width: 90%;
    margin-left: 5%;
  }
  .wrapper .appWrapper .appLeft .appContent .appLogo {
    position: absolute;
    top: -36px;
    left: 50%;
    transform: translateX(-40px);
  }
  .wrapper .appWrapper .appLeft .appContent .appTitle {
    padding-top: 76px;
  }
  .wrapper .appWrapper .appLeft .appContent .detailWrapper {
    margin-top: 40px;
  }
  .wrapper .appWrapper .appLeft .appContent .detailWrapper .detail {
    font-size: 1em;
  }
  .wrapper .appWrapper .appLeft .buttons {
    display: none;
    position: absolute;
    bottom: 60px;
    left: 50%;
    height: 100px;
    transform: translateX(-47px);
    width: 92%;
  }
  .wrapper .appWrapper .appTeaser {
    left: 0;
    margin-top: 60px;
    margin-bottom: 40px;
    margin-left: 4.06%;
    width: 91.47%;
    height: auto;
    position: relative;
  }
  .slider {
    height: 100%;
  }
  .slider :global .slick-dots {
    display: block;
    bottom: 100px;
  }
  .slider :global .slick-dots li.slick-active button:before {
    opacity: 1;
    color: #1890ff;
  }
}
